import React, { Component } from 'react';
import TextField from 'material-ui/TextField';
import Paper from 'material-ui/Paper';
import AppBar from 'material-ui/AppBar';
import Subheader from 'material-ui/Subheader';
import ReactCSSTransitionGroup from 'react-addons-css-transition-group' // ES6
import FlatButton from 'material-ui/FlatButton';
import { connect } from 'react-redux';
import Snackbar from 'material-ui/Snackbar';

 class Main extends Component{

constructor(props) {
		    super(props);
		    this.state = {
		    	length: 0,
		    	subjects: [{name:' ', codename: 'math'},
            {name:' ', codename: 'math_pro'},
             {name:'', codename: 'chemistry'}],
             	chooseSub: [],
             	message: '',
             	open: false
		    };
		  }


		  send = (city, bio, subjects)=>{
		  	const{token} = this.props.user
		  	var xmlhttp = new XMLHttpRequest()
			var body =  JSON.stringify({token: token, city: city, about: bio, subjects: subjects })  
			xmlhttp.open('POST', 'http://127.0.0.1:9999/api/user/done_create_page', false);
			xmlhttp.setRequestHeader("Content-Type", "application/json;charset=UTF-8");
			xmlhttp.send(body);  
			if(xmlhttp.status == 200) {
			var data = JSON.parse(xmlhttp.responseText)
			if (data.result != 'Error' ){
				window.location.href = '/mypage'
			}
			}
		  }

		  changeBio=(value)=>{
		  	var length = value.length;
		  	this.setState({
		  		length: length
		  	})
		  };

  	 handleTouchTap = () => {
			    this.setState({
			      open: true,
			    });
			  };
		handleRequestClose = () => {
		    this.setState({
		      open: false,
		    });
		  };

	validation=()=>{
		function d(id){
			return(document.getElementById(id).value)
		}
		var city=d('city')
		var bio = d('bioInput')
		 if(this.state.chooseSub.length<1){
		 	this.setState({
		 		message: '    '
		 	})
		 	this.handleTouchTap()
		 	return(0)
		 }else{
		 		this.send(city, bio, this.state.chooseSub)
		 }


	}

	handllink=(codename)=>{
		var realmas = this.state.chooseSub
		var message = ''
				if(realmas.indexOf( codename ) != -1 ){
					 realmas.splice(realmas.indexOf( codename ), 1)
				}else{
					if(realmas.length<3){
						realmas.push(codename)
					}else{
						message = ' - 3 '
					}
				}
				this.setState({
					chooseSub: realmas,
					message: message,
				})
		  	}


 getSubjects=()=>{
 	var subjects = this.state.subjects.map(function(item, index){
		var backstyle = {
			background: 'url(http://127.0.0.1:9999/subject_pic/' +item.codename+ ')',
			backgroundSize: "183%",
			backgroundPositionX: "50%",
			backgroundRepeat: 'no-repeat',
		}	
		var namecalss='coub-inside '
		var zDepth = 3
			if(this.state.chooseSub.indexOf( item.codename ) != -1 ){
				 namecalss='coub-inside chooseSub'
				 zDepth =1
			}

			return(
				<div key={index} className="coub" onClick={()=>this.handllink(item.codename)}>
					<Paper style={backstyle} className={namecalss} zDepth={zDepth}/>
					<Subheader style={{paddingLeft: 0,position: "relative", textAlign: 'center', lineHeight: 1}}>{item.name}</Subheader>
				</div>
				)


		}.bind(this))
 	return(subjects)
 }

render(){
	const style={
		up:{
			margin: 'auto',
			fontSize: 24,
			color: '#fff',
			padding: 60,
		}
	}
	const styles = {
				  
				  underlineStyle: {
				    borderColor: "#2196F3",
				  },
				  floatingLabelFocusStyle: {
				    color: "#2196F3",
				  },
				};

	var subjects=this.getSubjects()
 

	
	return(<div className="Up">
		<div style={{maxWidth: 500, margin: 'auto', marginTop: 100}} id="mainpaper">
			<Paper id="paper1">
			<AppBar  title=" " id="menu"
			showMenuIconButton={false} style={{background: '#2196F3'}}/>
			<div style={{padding: 20}}>
			<Subheader style={{paddingLeft: 0}}> </Subheader>
			     <TextField
				     floatingLabelFocusStyle={styles.floatingLabelFocusStyle}
			      	underlineFocusStyle={styles.underlineStyle}
				      hintText=""
				      id='city'
			    /><br />
			     <TextField
			      		floatingLabelFocusStyle={styles.floatingLabelFocusStyle}
				      	underlineFocusStyle={styles.underlineStyle}
				      	fullWidth={true}
				      	maxLength="256"
				      	onChange={(event: object, newValue: string)=>this.changeBio(newValue)}
				      	id="bioInput"
				      	multiLine={true}
						rows={2}
						rowsMax={3}
						fullWidth={true}
						multiLine={true}
			      		hintText=" "
	    		/><br />
	    		<div className="characterCounter"><div id='countElem'>{this.state.length}</div>/256</div><br />
	    		<Subheader style={{paddingLeft: 0}}></Subheader>
	    		<div className='subjectLine'>
					<ReactCSSTransitionGroup
					transitionName="example"
               transitionAppear = {true} transitionAppearTimeout = {2000}
               transitionEnter = {false} transitionLeave = {false}>
					{subjects}
					</ReactCSSTransitionGroup>
    			</div>
    			<div className="characterCounter"><div id='countElem'>{this.state.chooseSub.length}</div>/3</div><br />
    			<FlatButton label="!" onClick={()=>this.validation()}
    			 style={{color: '#2196F3', margin: 10, marginLeft: 0, position: 'relative', top: -40}} />
    			</div>
    			
    			</Paper>
		</div>
		<Snackbar
						bodyStyle={{background: 'rgba(0,0,0,0.6)'}}
			          open={this.state.open}
			          message={this.state.message}
			          autoHideDuration={4000}
			          onRequestClose={this.handleRequestClose}
			        />
		</div>

		

		   )

}



}
function mapStateToProps (state) {
  return {
    user: state.user
  }
}



export default connect(mapStateToProps)(Main)


